::-webkit-scrollbar {
    width: 0px;  /* remove scrollbar space */
    background: transparent;  /* optional: just make scrollbar invisible */
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#home-scroll-helper {
  position: absolute;
  width: 100%;
  height: 200%;
  display: block;
  visibility: hidden
}

#home-scroll-wrapper {
  position: absolute;
  width: 100%;
  height: 200%;
  display: block;
}

 
.letter-k{  /* responsive */
  position: absolute;
  top: 25%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 5;
  -webkit-transition: opacity 1s ease-in-out 0s;
  -o-transition: opacity 1s ease-in-out 0s;
  transition: opacity 1s ease-in-out 0s;
  height: 15%;
  -webkit-filter: drop-shadow(0 0 10px rgba(35, 96, 206, 0.27) );
          filter: drop-shadow(0 0 10px rgba(35, 96, 206, 0.27) )
}

.letter-k-hidden {
  opacity: 0;
}

.letter-k-show {
  opacity: 1;
}

#intro {
  position: absolute;
  top: 75%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 5;
  opacity: 0;
  -webkit-transition: opacity 1s ease-in-out 0s;
  -o-transition: opacity 1s ease-in-out 0s;
  transition: opacity 1s ease-in-out 0s;
  color: #F4F4F4;
  font-family: 'Noto Sans', sans-serif;
}

#intro span {
  display: block;
  line-height: 50px;
}

.intro-show {
  -webkit-transition: opacity 1s ease-in-out 0s;
  -o-transition: opacity 1s ease-in-out 0s;
  transition: opacity 1s ease-in-out 0s;
  opacity: 1 !important;
}

#portrait {
  border-radius: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}




.home-content-hidden {
  opacity: 0;
}

.home-content-show {
  opacity: 1;
}

#home-content {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: auto;
  z-index: 5;
  -webkit-transition: opacity 1s cubic-bezier(0.51, 0.6, 0.17, 1);
  -o-transition: opacity 1s cubic-bezier(0.51, 0.6, 0.17, 1);
  transition: opacity 1s cubic-bezier(0.51, 0.6, 0.17, 1);
  /*animation: home-appear 1s 1;
  animation-delay: 1s;
  animation-fill-mode: forwards;*/
}



